import React from 'react'
import Scrolling from 'react-horizontal-scrolling-menu'
import Card from './categoryCards.js'
export default class Category extends React.Component{
	constructor(props){
		super(props)
		this.list1=[
		{text:"Today's Current",style:'1',secondText:'Economy',secondStyle:'2'},
		{text:'National',style:'3',secondText:'Envirnoment',secondStyle:'4'},
		{text:'EconomicSurvey',style:'1',secondText:'Political',secondStyle:'2'},
		{text:'Environment',style:'3',secondText:'Science&Tech',secondStyle:'4'},
		{text:'International',style:'1',secondText:'Budget',secondStyle:'2'},
		{text:'Art',style:'3',secondText:'Schemes',secondStyle:'4'},
		{text:'Report"s',style:'1',secondText:'Geography',secondStyle:'2'},
		{text:'International Bodies',style:'3',secondText:'Economic Survey',secondStyle:'4'},
		{text:'India Year Book',style:'1',secondText:'Download',secondStyle:'2'},
		]
		this.clicked = this.clicked.bind(this)
		this.newkey=10
		console.log(this.newkey)
		
	}
	clicked(value){
		this.props.category(value)
	}
	render(){
		return(
			<div className="category mr-2 p-3 mt-5 mb-3">
				<Scrolling
				data={
					this.list1.map((data,key)=>(
					<div>
					<Card style={data.style} text={data.text} key={data.text} onClick={()=>this.clicked(data.text)} />
					<Card style={data.secondStyle} text={data.secondText} key={data.secondText} onClick={()=>this.clicked(data.secondText)}/> 
					</div>
					))
				}
				alignCenter={false}
				scrollBy={1}
				hideArrows={true}
				/>
				
			</div>
			)
	}
}